/* Section 1 */

.banner {
  text-align: center;
}

.banner img {
  width: 100vw;
}

.banner .inputemail {
  height: 43px;
  border-radius: 3px;
  border: 2px solid rgba(128, 128, 128, 0.651);
  width: 90%;
  margin: 20px 20px 8px;
}

input[type="email"] {
  padding: 0 10px;
}

.banner input::placeholder {
  opacity: 40%;
}

